<template>
  <!-- component status start -->
  <div>
      <h1>Component Status</h1>
      <!-- status description start -->
      <ul class="status-description">
        <li>
          <svg aria-hidden="true" class="ico color-done">
            <use xlink:href="#ico-done"></use>
          </svg>
          Ready
        </li>
        <li>
          <svg aria-hidden="true" class="ico color-inprogress">
            <use xlink:href="#ico-inprogress"></use>
          </svg>
          In Progress
        </li>
        <li>
          <svg aria-hidden="true" class="ico color-not-avalible">
            <use xlink:href="#ico-not-avalible"></use>
          </svg>
          Not avalible
        </li>
      </ul>
      <!-- status description end -->
      <!-- table description start -->
      <table class="table-description">
          <tr>
              <th class="oredr centered">Order</th>
              <th>Component</th>
              <th class="centered">Status</th>
              <th>Time Estimate</th>
          </tr>
          <tr v-for = "(component, key) in components">
            <td class="order centered">{{ key + 1 }}</td>
            <td>{{ component.name }}</td>
            <td class="centered">
              <svg aria-hidden="true" class="ico" :class="'color-' + component.status">
                <use :xlink:href="'#ico-' + component.status"></use>
              </svg>
            </td>
            <td><!--{{ component.estimate }} h-->Release 31.01</td>
          </tr>
      </table>
      <!-- table description end -->
      <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style="display: none;">
        <symbol id="ico-done">
          <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"/><polyline points="22 4 12 14.01 9 11.01"/>
        </symbol>
        <symbol id="ico-inprogress">
          <path d="M20 14.66V20a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V6a2 2 0 0 1 2-2h5.34"/><polygon points="18 2 22 6 12 16 8 16 8 12 18 2"/>
        </symbol>
        <symbol id="ico-not-avalible">
          <circle cx="12" cy="12" r="10"/><path d="M16 16s-1.5-2-4-2-4 2-4 2"/><line x1="9" y1="9" x2="9.01" y2="9"/><line x1="15" y1="9" x2="15.01" y2="9"/>
        </symbol>
      </svg>
  </div>
  <!-- component status end -->
</template>

<script>
	export default {
		props: {
		},
		data(){
			return {
        startDate: '17.01',
        components: 
        [
          { name: 'Typography', status: 'inprogress', estimate: 10 },
          { name: 'Colors', status: 'inprogress', estimate: 4 },
          { name: 'Page Layout', status: 'inprogress', estimate: 5 },
          { name: 'SVG Icon', status: 'inprogress', estimate: 8 },
          { name: 'Radio', status: 'not-avalible', estimate: 4 },
          { name: 'Radio Group', status: 'not-avalible', estimate: 4 },
          { name: 'Checkbox Group', status: 'not-avalible', estimate: 4 },
          { name: 'Checkbox', status: 'not-avalible', estimate: 4 },
          { name: 'Accordion', status: 'not-avalible', estimate: 4 },
          { name: 'Tooltip', status: 'not-avalible', estimate: 4 },
          { name: 'Card', status: 'not-avalible', estimate: 4 },
          { name: 'Pill', status: 'not-avalible', estimate: 4 },
          { name: 'Table', status: 'not-avalible', estimate: 4 },
          { name: 'Breadcrumbs', status: 'not-avalible', estimate: 4 },
          { name: 'Page Header', status: 'not-avalible', estimate: 4 },
          { name: 'Tabs', status: 'not-avalible', estimate: 4 },
          { name: 'Pagination', status: 'not-avalible', estimate: 4 },
          { name: 'Button', status: 'not-avalible', estimate: 4 },
          { name: 'Button Group', status: 'not-avalible', estimate: 4 },
          { name: 'Modal', status: 'not-avalible', estimate: 4 },
          { name: 'Input', status: 'not-avalible', estimate: 4 },
          { name: 'Form Item', status: 'not-avalible', estimate: 4 },
          { name: 'Search', status: 'not-avalible', estimate: 4 },
          { name: 'Tag', status: 'not-avalible', estimate: 4 },
          { name: 'Dropdown', status: 'not-avalible', estimate: 4 },
          { name: 'Form Group', status: 'not-avalible', estimate: 4 },
          { name: 'Icon List', status: 'not-avalible', estimate: 4 },
          { name: 'Page Footer', status: 'not-avalible', estimate: 4 },
          { name: 'Main Nav', status: 'not-avalible', estimate: 4 },
          { name: 'Toast', status: 'not-avalible', estimate: 4 },
          { name: 'Figure', status: 'not-avalible', estimate: 4 },
          { name: 'Grid', status: 'not-avalible', estimate: 10 },
        ]
      };
    },
	}
</script>

<style>
  /* status-description */
  .status-description {
    margin: 0 0 30px;
    font-size: 18x;
  }
  .status-description li {
    display: inline-flex;
    align-items: center;
    margin-right: 15px;
  }
  .status-description .ico { margin-right: 10px; }
  .color-done { color: #55b149; }
  .color-inprogress { color: #1178ce; }
  .color-not-avalible { color: #a9abb6; }
</style>


