<template>
  <div>
    <h1>Project Settings</h1>
    <div class="text-group">
        <p>Project settings is a file which contains a list of globaly used variables like colors, sizes, fonts, etc. Defining them in a single place helps reduce required code changes in case any of the global settings need to be modified. This file can be located at 'global/settings.scss'.</p>
    </div>
    <h3>Brand Colors</h3>
    <table class="table-description">
        <tr>
          <th>Variable</th><th>Default State</th>
          <th>Description</th>
        </tr>
        <tr>
          <td class="var-name">$g-color-primary:</td>
          <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorPrimary">${{ QuaverDefaultSettings.ColorPrimary }}</span></td>
          <td>Primanry Color (buttons, links)</td>
        </tr>
        <tr>
          <td class="var-name">$g-color-primary-hover:</td>
          <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorPrimaryHover">${{ QuaverDefaultSettings.ColorPrimaryHover }}</span></td>
          <td>Primanry Hover Color (hover state of buttons, links)</td>
        </tr>
        <tr>
          <td class="var-name">$g-color-secondary:</td>
          <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorSecondary">${{ QuaverDefaultSettings.ColorSecondary }}</span></td>
                <td>Secondary Color</td>
        </tr>
        <tr>
                <td class="var-name">$g-color-secondary-hover:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorSecondaryHover">${{ QuaverDefaultSettings.ColorSecondaryHover }}</span></td>
                <td>Secondary Color Hover</td>
        </tr>
        <tr>
                <td class="var-name">$g-color-state-error:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorStateError">${{ QuaverDefaultSettings.ColorStateError }}</span></td>
                <td>Coloir State Error</td>
        </tr>
        <tr>
                <td class="var-name">$g-color-state-success:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorStateSuccess">${{ QuaverDefaultSettings.ColorStateSuccess }}</span></td>
                <td>Coloir State Success</td>
        </tr>
        <tr>
                <td class="var-name">$g-color-state-inactive:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.ColorStateInactive">${{ QuaverDefaultSettings.ColorStateInactive }}</span></td>
                <td>Color State Inactive</td>
        </tr>
        <tr>
                <td class="var-name">$g-bg-primary:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.PrimaryBg">${{ QuaverDefaultSettings.PrimaryBg }}</span></td>
                <td>Primary Background</td>
        </tr>
        <tr>
                <td class="var-name">$g-text-color-primary:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.TextColorPrimary">${{ QuaverDefaultSettings.TextColorPrimary }}</span></td>
                <td>Primary Text Color</td>
        </tr>
        <tr>
                <td class="var-name">$g-text-color-secondary:</td>
                <td><span class="color-info" v-bind:class="QuaverDefaultSettings.TextColorSecondary">${{ QuaverDefaultSettings.TextColorSecondary }}</span></td>
                <td>Secondary Text Color</td>
        </tr>
</table>
<h3>Fonts</h3>
    <table class="table-description">
        <tr>
                <th class="var-name">Variable</th>
                <th>Default State</th>
                <th>Description</th>
        </tr>
        <tr>
                <td class="var-name">$g-font-size:</td>
                <td>{{ QuaverDefaultSettings.FontSize }}px</span></td>
                <td>Primary Font Size</td>
        </tr>
        <tr>
                <td class="var-name">$g-font-size-mobile:</td>
                <td>{{ QuaverDefaultSettings.FontSizeMobile }}px</span></td>
                <td>Primary Font Size (Mobile):</td>
        </tr>
        <tr>
                <td class="var-name">$g-font-family-primary:</td>
                <td>{{ QuaverDefaultSettings.FontFamilyPrimery }}</span></td>
                <td>Primary Font Family (body, p, ul, table, etc.)</td>
        </tr>
        <tr>
                <td class="var-name">$g-font-family-secondary:</td>
                <td>{{ QuaverDefaultSettings.FontFamilySecondary }}</span></td>
                <td>Secondary Font Family (titles)</td>
        </tr>
        <tr>
                <td class="var-name">$g-font-family-tertialy:</td>
                <td>{{ QuaverDefaultSettings.FontFamilyTertialy }}</span></td>
                <td>Tertialy Font Family (titles)</td>
        </tr>
</table>
<h3>Spacing</h3>
    <table class="table-description">
        <tr>
                <th class="var-name">Variable</th>
                <th>Default State</th>
                <th>Description</th>
        </tr>
        <tr>
                <td class="var-name">$g-element-spacing-verticaly:</td>
                <td>{{ QuaverDefaultSettings.ElementSpacing }}</span></td>
                <td>Common verticaly space between elements</td>
        </tr>
        <tr>
                <td class="var-name">$g-element-spacing-verticaly-mobile:</td>
                <td>{{ QuaverDefaultSettings.ElementSpacingMobile }}</span></td>
                <td>Common verticaly space between elements (mobile)</td>
        </tr>
</table>
<h3>Borders</h3>
    <table class="table-description">
        <tr>
                <th class="var-name">Variable</th>
                <th>Default State</th>
                <th>Description</th>
        </tr>
        <tr>
                <td class="var-name">$g-border-radius:</td>
                <td>{{ QuaverDefaultSettings.BorderRadius }}</span></td>
                <td>Common Border radius (buttons, tooltip, inputs)</td>
        </tr>
        <tr>
                <td class="var-name">$g-border-width:</td>
                <td>{{ QuaverDefaultSettings.BorderWidth }}</span></td>
                <td>Common Border Width (buttons, tooltip, inputs)</td>
        </tr>
    </table>	
  </div>
</template>
<script>
  import quaverDefaultSettings from '../../mixins/quaverDefaultSettings';
  export default {
        props: {
        },
        data(){
                return {
                
                };
        },
        computed: {

        }
}
</script>


