<template>
  <!-- colors section start -->
  <div>
    <h1>Colors</h1>
    <p>Design information about colors styles can be found at <a href="https://ascapny.atlassian.net/wiki/spaces/PD/pages/524091393/Tokens" target="_blank">Confluence</a>.</p>
    <div class="color-wrap">
      <h2>Project Settings</h2>
      <h4>Brand Colors</h4>
      <ul class="color-list">
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorPrimary)" :class="'t-' + this.QuaverDefaultSettings.ColorPrimary" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorPrimary) }">{{ getColor(this.QuaverDefaultSettings.ColorPrimary) }}</button>
            <div class="color-settings-desc">
              <strong>Primanry Color</strong>
              <span>{{ this.QuaverDefaultSettings.ColorPrimary }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorPrimaryHover)" :class="'t-' + this.QuaverDefaultSettings.ColorPrimaryHover" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorPrimaryHover) }">{{ getColor(this.QuaverDefaultSettings.ColorPrimaryHover) }}</button>
            <div class="color-settings-desc">
              <strong>Primanry Color Hover</strong>
              <span>{{ this.QuaverDefaultSettings.ColorPrimaryHover }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorSecondary)" :class="'t-' + this.QuaverDefaultSettings.ColorSecondary" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorSecondary) }">{{ getColor(this.QuaverDefaultSettings.ColorSecondary) }}</button>
            <div class="color-settings-desc">
              <strong>Secondary Color</strong>
              <span>{{ this.QuaverDefaultSettings.ColorSecondary }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorSecondaryHover)" :class="'t-' + this.QuaverDefaultSettings.ColorSecondaryHover" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorSecondaryHover) }">{{ getColor(this.QuaverDefaultSettings.ColorSecondaryHover) }}</button>
            <div class="color-settings-desc">
              <strong>Secondary Color Hover</strong>
              <span>{{ this.QuaverDefaultSettings.ColorSecondaryHover }}</span>
            </div>
        </li>
      </ul>
      <h4>State Colors</h4>
      <ul class="color-list">
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorStateError)" :class="'t-' + this.QuaverDefaultSettings.ColorStateError" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorStateError) }">{{ getColor(this.QuaverDefaultSettings.ColorStateError) }}</button>
            <div class="color-settings-desc">
              <strong>Color State Error</strong>
              <span>{{ this.QuaverDefaultSettings.ColorStateError }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorStateSuccess)" :class="'t-' + this.QuaverDefaultSettings.ColorStateSuccess" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorStateSuccess) }">{{ getColor(this.QuaverDefaultSettings.ColorStateSuccess) }}</button>
            <div class="color-settings-desc">
              <strong>Color State Success</strong>
              <span>{{ this.QuaverDefaultSettings.ColorStateSuccess }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.ColorStateInactive)" :class="'t-' + this.QuaverDefaultSettings.ColorStateInactive" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.ColorStateInactive) }">{{ getColor(this.QuaverDefaultSettings.ColorStateInactive) }}</button>
            <div class="color-settings-desc">
              <strong>Color State Inactive</strong>
              <span>{{ this.QuaverDefaultSettings.ColorStateInactive }}</span>
            </div>
        </li>
      </ul>
      <h4>Typography Colors</h4>
      <ul class="color-list">
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.PrimaryBg)" :class="'t-' + this.QuaverDefaultSettings.PrimaryBg" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.PrimaryBg) }">{{ getColor(this.QuaverDefaultSettings.PrimaryBg) }}</button>
            <div class="color-settings-desc">
              <strong>Primary Bg</strong>
              <span>{{ this.QuaverDefaultSettings.PrimaryBg }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.TextColorPrimary)" :class="'t-' + this.QuaverDefaultSettings.TextColorPrimary" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.TextColorPrimary) }">{{ getColor(this.QuaverDefaultSettings.TextColorPrimary) }}</button>
            <div class="color-settings-desc">
              <strong>Text Color Primary</strong>
              <span>{{ this.QuaverDefaultSettings.TextColorPrimary }}</span>
            </div>
        </li>
        <li>
            <button v-clipboard="() => getColor(this.QuaverDefaultSettings.TextColorSecondary)" :class="'t-' + this.QuaverDefaultSettings.TextColorSecondary" :style="{ backgroundColor: getColor(this.QuaverDefaultSettings.TextColorSecondary) }">{{ getColor(this.QuaverDefaultSettings.TextColorSecondary) }}</button>
            <div class="color-settings-desc">
              <strong>Text Color Secondary</strong>
              <span>{{ this.QuaverDefaultSettings.TextColorSecondary }}</span>
            </div>
        </li>
      </ul>
      <h2>Site Colors</h2>
      <h4>Blue</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'b'">
            <button v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
      <h4>Pink</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'p'">
            <button  v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
      <h4>Green</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'g'">
            <button  v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
      <h4>Red</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'r'">
            <button v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
      <h4>Yellow</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'y'">
            <button v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
      <h4>Grayscale</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'm'">
            <button v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
      <h4>Neutral</h4>
      <ul class="color-list">
        <template v-for = "color in colors">
          <li v-if="color.name.slice(0,1) == 'n'">
            <button v-clipboard="() => color.hex" :class="'t-' + color.name" :style="{ backgroundColor: color.hex }">{{ color.hex }}</button>
            <div class="color-settings-desc"><span>{{ color.name }}</span></div>
          </li>
        </template>
      </ul>
    </div>
  </div>
  <!-- colors section end -->
</template>
<script>
  import Vue from 'vue';
  import Clipboard from 'v-clipboard';
  Vue.use(Clipboard);
	export default {
    
		props: {
		},
		data(){
			return {
        colors: 
        [
          { hex: '#002d5b', name: 'b700' },
          { hex: '#004183', name: 'b600' },
          { hex: '#175da7', name: 'b500' },
          { hex: '#1178ce', name: 'b400' },
          { hex: '#489cef', name: 'b300' },
          { hex: '#4dbdff', name: 'b200' },
          { hex: '#89d3ff', name: 'b100' },
            
          { hex: '#7d287e', name: 'p300' },
          { hex: '#b43ab5', name: 'p200' },
          { hex: '#d281d2', name: 'p100' },
        
          { hex: '#288837', name: 'g300' },
          { hex: '#55b149', name: 'g200' },
          { hex: '#80d462', name: 'g100' },
           
          { hex: '#ce3535', name: 'r300' },
          { hex: '#eb6363', name: 'r200' },
          { hex: '#f89595', name: 'r100' },
           
          { hex: '#f4ca08', name: 'y300' },
          { hex: '#fbe16b', name: 'y200' },
          { hex: '#fef59b', name: 'y100' },
          
          { hex: '#000', name: 'm900' },
          { hex: '#0d0d0d', name: 'm800' },
          { hex: '#373737', name: 'm700' },
          { hex: '#6d6d6d', name: 'm600' },
          { hex: '#a7a7a7', name: 'm500' },
          { hex: '#cecece', name: 'm400' },
          { hex: '#ededed', name: 'm300' },
          { hex: '#fafafa', name: 'm200' },
          { hex: '#fff', name: 'm100' },
            
          { hex: '#4a4d56', name: 'n600' },
          { hex: '#85868c', name: 'n500' },
          { hex: '#a9abb6', name: 'n400' },
          { hex: '#d8dbe2', name: 'n300' },
          { hex: '#eef0f5', name: 'n200' },
          { hex: '#f7f9fb', name: 'n100' },
          
        ],
			};
    },
		methods: {
      getColor:function(name) {
        var color = '';
        this.colors.forEach(function(item, i, arr) {
          if (arr[i].name == name) color = arr[i].hex;
        });
        return color;
      },
    }
	}
</script>
<style>
  .color-list {
    margin: 0 0 30px;
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
  }
  .color-list li {
    margin: 0 -1px 15px 0;
  }
  .color-list button {
    display: block;
    width: 150px;
    height: 90px;
    border: 0;
    cursor: pointer;
    transition: all 0.1s;
    color: #fff;
    transition: all 0.1s
  }
  .color-list button:active { 
    position: relative;
    font-size: 0;
    transition: 0;
  }
  .color-list button:hover {
    margin:-5px;
    width: 160px;
    height: 100px;
    position: relative;
  }
  .color-list button:after {
    content: "copy";
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -12px 0 0 -25px;
    width: 50px;
    font-size: 12px;
    line-height: 20px; 
    border: 2px solid currentColor;
    opacity: 0;
    transition: all 0.1s
  }
  .color-list button:active:after {
    opacity: 1;
    transition: 0;
  }
  .color-settings-desc {
    margin: 10px 0 0;
  }
  .color-settings-desc strong { display: block; }
  .color-wrap h3 {
    color: #444;
  }
  .t-b100, .t-g100, .t-r100, .t-y100, .t-y200, .t-m400, .t-m300, .t-m200, .t-m100, .t-n300, .t-n200, .t-n100  { color: #000 !important; }
  .t-m100 { border: 1px solid #ccc !important; }
  h4 {
    font-family: 'Circular Book', sans-serif;
    font-weight: normal;
    margin: 0 0 10px;
  }
</style>


