<template>
  <!-- SVGIcon section start -->
  <div>
    <!-- component content start -->
    <div class="component-content">
      <h1>SVG</h1>
      <div class="text-content">
        <p>We use <span class="class-name">c-icon</span> component to render system icon.</p>
        <p>This component symbolizes a command, file, device, or a directory. System icons are also used to represent common actions like trash, print, and save, and are commonly found in app bars, toolbars, buttons, and lists.</p>
      </div>
      <!-- using example start -->
      <div class="using-example">
          <!-- view tab start -->
          <input name="tabs-08" type="radio" id="icon-preview-08" checked="checked" class="using-example__input"/>
          <label for="icon-preview-08" class="using-example__label first">Preview</label>
          <div class="using-example__panel">
            <div class="view">
              <!-- desktop start -->
              <div class="view__column desktop">
                <div class="view__content">
                    <c-icon 
                    :icon = "activesvg"
                    :size = "activeModifierSize"
                    :weight = "activeModifierWeight"
                  >
                    {{ activesvg }}
                  </c-icon>
                  {{ getTitle }}
                </div>
              </div>
              <!-- desktop end -->
              <!-- mobile start -->
              <div class="view__column mobile">
                  <div class="view__content">
                      <c-icon 
                      :icon = "activesvg"
                      :size = "activeModifierSize"
                      :weight = "activeModifierWeight"
                    >
                      {{ activesvg }}
                    </c-icon>
                    {{ getTitle }}
                  </div>
                </div>
              <!-- mobile end -->
            </div>
          </div>
          <!-- view tab end -->
          <!-- markup tab start -->
          <input name="tabs-08" type="radio" id="icon-markup-08" class="using-example__input"/>
          <label for="icon-markup-08" class="using-example__label last">Markup</label>
          <div class="using-example__panel">
            <pre>
&lt;c-icon<span class="atr"> icon="<span class="atram">{{ this.activesvg }}</span>"</span><span v-if="(activeModifierSize != '')&&(activeModifierSize != 'default')" class="atr"> size="<span class="atram">{{ activeModifierSize }}</span>"</span><span v-if="(activeModifierWeight != '')&&(activeModifierWeight != 'default')" class="atr"> weight="<span class="atram">{{ activeModifierWeight }}</span>"</span>&gt;{{ getTitle }}&lt;/c-icon&gt; {{ getTitle }}
            </pre>
          </div>
          <!-- markup tab end -->
      </div>
      <!-- using example end -->
      <div class="text-content">
        <h3>Default Style</h3>
        <p>Most of our icons use design from <a href="https://feathericons.com/" target="_blank">feathericons</a>. Additional information about icons styles can be found at <a href="https://ascapny.atlassian.net/wiki/spaces/PD/pages/524091393/Tokens" target="_blank">Confluence</a>.</p>
        <p>Default color of an icon inherits the parent text color. Default icon size depends on the parent text size as well, it's increased by 12.5%. For example, for the font size 16px width and height of an icon will be 18px.</p>
        <img src="./../../documentation-components/SvgIcon/images/svg-icon.png" alt="" width="250px" />
      </div>
      <div class="text-content">
        <h3>Icon Attribute:</h3>
        <p><span class="class-name">c-icon</span> component has its own svg icon library inside. In order to chose an icon you need to use <em>icon</em> attribute with the specific name.</p>
      </div>
      <div class="text-content">
        <h3>Size Attribute:</h3>
        <p>This attribute can be used in cases when an icon size needs to be decoupled from the parent text size and it needs to be defined manualy.</p>
        <table class="table-description">
          <tr>
            <th class="attr">Size</th>
            <th class="description">Description</th>
            <th class="example centered">Example</th>
          </tr>
          <tr>
            <td>
              <span class="attr-name">default</span>
            </td>
            <td class="description">
              Flexible size is 1.125em x 1.125 em, it depends on the current font size (18 x 18 px for the default font size 16px)
            </td>
            <td class="centered">
              <c-icon :icon = "activesvg">{{ activesvg }}</c-icon>
            </td>
          </tr>
          <tr>
            <td>
              <span class="attr-name">small</span>
            </td>
            <td class="description">
              Fixed size is 30 x 30 px
            </td>
            <td class="centered">
              <c-icon :icon = "activesvg" size="small">{{ activesvg }}</c-icon>
            </td>
          </tr>
          <tr>
            <td>
              <span class="attr-name">medium</span>
            </td>
            <td class="description">
              Fixed size is 50 x 50 px
            </td>
            <td class="centered">
              <c-icon :icon = "activesvg" size="medium">{{ activesvg }}</c-icon>
            </td>
          </tr>
          <tr>
            <td>
              <span class="attr-name">large</span>
            </td>
            <td class="description">
              Fixed size is 75 x 75 px
            </td>
            <td class="centered">
              <c-icon :icon = "activesvg" size="large">{{ activesvg }}</c-icon>
            </td>
          </tr>
          <tr>
            <td>
              <span class="attr-name">xlarge</span>
            </td>
            <td class="description">
              Fixed size is 100 x 100 px
            </td>
            <td class="centered">
              <c-icon :icon = "activesvg" size="xlarge">{{ activesvg }}</c-icon>
            </td>
          </tr>
          <tr>
            <td>
              <span class="attr-name">xxlarge</span>
            </td>
            <td class="description">
              Fixed size is 150 x 150 px
            </td>
            <td class="centered">
              <c-icon :icon = "activesvg" size="xxlarge">{{ activesvg }}</c-icon>
            </td>
          </tr>
        </table>
      </div>
      <div class="text-content">
          <h3>Weight Attribute:</h3>
          <p>This attribute can be used to define an icon's line width.</p>
          <table class="table-description">
            <tr>
              <th class="attr">Weight</th>
              <th class="description">Description</th>
              <th class="example centered">Example</th>
            </tr>
            <tr>
              <td>
                <span class="attr-name">default</span>
              </td>
              <td class="description">
                Line width is 2 px
              </td>
              <td class="centered">
                <c-icon :icon = "activesvg">{{ activesvg }}</c-icon>
              </td>
            </tr>
            <tr>
              <td>
                <span class="attr-name">tiny</span>
              </td>
              <td class="description">
                Line width is 1 px
              </td>
              <td class="centered">
                <c-icon :icon = "activesvg" weight="tiny">{{ activesvg }}</c-icon>
              </td>
            </tr>
            <tr>
              <td>
                <span class="attr-name">medium</span>
              </td>
              <td class="description">
                Line width is 3 px
              </td>
              <td class="centered">
                <c-icon :icon = "activesvg" weight="heavy">{{ activesvg }}</c-icon>
              </td>
            </tr>
          </table>
        </div>
    </div>
    <!-- component content end -->
    <!-- properties content start -->
    <aside class="properties-content">
      <!-- parameters list start -->
      <dl class="parameters">
        <dt>Icon Attribute:</dt>
        <dd v-for = "svg in svg.icon" v-bind:class="[ activesvg === svg.name ? 'is-active' : '' ]" class="icons">
          <button v-on:click="activesvg = svg.name" draggable="false" :data-tooltip="svg.title" class="tooltip">
            <c-icon :icon = "svg.name">{{ svg.title }}</c-icon>
          </button>
        </dd>
        <dt>Size Attribute:</dt>
        <dd v-for = "sizeModifier in svg.size" v-bind:class="[ activeModifierSize === sizeModifier ? 'is-active' : '' ]">
          <button v-on:click="activeModifierSize = sizeModifier">
            {{ sizeModifier }}
          </button>
        </dd>
        <dt>Weight Attribute:</dt>
        <dd v-for = "weightModifier in svg.weight" v-bind:class="[ activeModifierWeight === weightModifier ? 'is-active' : '' ]">
          <button v-on:click="activeModifierWeight = weightModifier">
            {{ weightModifier }}
          </button>
        </dd>
      </dl>
      <!-- parameters list end -->
    </aside>
    <!-- properties content end -->
  </div>
  <!-- SVGIcon section end -->
  </div>
</template>
<script>
	export default {
		props: {
		},
		data(){
			return {
        activesvg: 'search',
        activeModifierSize: 'default',
        activeModifierWeight: 'default',
        svg: {
					icon: 
					[
						{ name: 'search', title: 'Search' },
						{ name: 'share', title: 'Share' },
						{ name: 'download', title: 'Download' },
						{ name: 'print', title: 'Print' },
						{ name: 'collapse', title: 'Collapse' },
						{ name: 'expand', title: 'Expand' },
						{ name: 'phone', title: 'Phone' },
						{ name: 'email', title: 'Email' },
						{ name: 'more-horizontal', title: 'More' },
						{ name: 'chevron-left', title: 'Previouse Page' },
						{ name: 'chevron-right', title: 'Next Page' },
						{ name: 'chevron-top', title: 'Expand' },
						{ name: 'chevron-down', title: 'Collapse' },
						{ name: 'chevrons-left', title: 'First Page' },
						{ name: 'chevrons-right', title: 'Last Page' },
						{ name: 'facebook', title: 'Facebook' },
						{ name: 'twitter', title: 'Twitter' },
						{ name: 'instagram', title: 'Instagram' },
						{ name: 'paperclip', title: 'Attached' }
					],
					size: [
						'default',
						'small',
						'medium',
						'large',
						'xlarge',
						'xxlarge'
					],
					weight: [
						'default',
						'tiny',
						'heavy'
					]
				},
			};
		},
		computed: {
      getTitle:function() {
        var b = this.activesvg;
        var a = this.svg.icon.find(x => x.name === this.activesvg);
        return a.title;
      },
    }
	}
</script>


