<template>
  <div id="app">
    <div class="container">
      <!-- main navigation start -->
      <nav class="main-nav">
				<h1 class="logo"><img src="./images/logo.svg" alt="ASCAP" />Quaver <span>Style Guide</span></h1>
				<h2>Base Styles</h2>
        <ul>
					<li :class="[ currentSection === 'project-settings' ? 'active' : '' ]"><a @click="currentSection = 'project-settings'" href="#">Project Settings</a></li>
					<li :class="[ currentSection === 'typography' ? 'active' : '' ]"><a @click="currentSection = 'typography'" href="#">Typography</a></li>
					<li :class="[ currentSection === 'colors' ? 'active' : '' ]"><a @click="currentSection = 'colors'" href="#">Color</a></li>
					<li :class="[ currentSection === 'page-layout' ? 'active' : '' ]"><a @click="currentSection = 'page-layout'" href="#">Page Layout</a></li>
					<!-- <li :class="[ currentSection === 'helpers' ? 'active' : '' ]"><a @click="currentSection = 'helpers'" href="#">Helpers</a></li> -->
				</ul>
				<h2>Components</h2>
				<ul>
						<li :class="[ currentSection === 'svg-icons' ? 'active' : '' ]"><a @click="currentSection = 'svg-icons'" href="#">SVG</a></li>
						<!-- <li :class="[ currentSection === 'form-item' ? 'active' : '' ]"><a @click="currentSection = 'form-item'" href="#">Form Item</a></li> -->
				</ul>
        <h2>Other</h2>
				<ul>
					<li :class="[ currentSection === 'implementation-plans' ? 'active' : '' ]"><a @click="currentSection = 'implementation-plans'" href="#">Implamentation Plans</a></li>
					<li :class="[ currentSection === 'component-status' ? 'active' : '' ]"><a @click="currentSection = 'component-status'" href="#">Component Status</a></li>
				</ul>
      </nav>
      <!-- main navigation end -->
      <!-- content start -->
      <main class="content">
        <section v-if="currentSection === 'project-settings'" >
          <doc-project-settings></doc-project-settings>
        </section>
        <section v-if="currentSection === 'typography'" >
          <doc-typography></doc-typography>
        </section>
        <section v-if="currentSection === 'colors'" >
          <doc-colors></doc-colors>
        </section>
        <section v-if="currentSection === 'page-layout'" >
          <doc-page-layout></doc-page-layout>
        </section>
        <section v-if="currentSection === 'svg-icons'" >
          <doc-svg-icon></doc-svg-icon>
        </section>
        <section v-if="currentSection === 'form-item'" >
          <doc-form-item></doc-form-item>
        </section>
        <section v-if="currentSection === 'implementation-plans'" >
          <doc-implementation-plans></doc-implementation-plans>
        </section>
        <section v-if="currentSection === 'component-status'" >
          <doc-component-status></doc-component-status>
        </section>
			</main>
      <!-- content end -->
    </div>
  </div>
</template>

<script>
import DocProjectSettings from './documentation-components/ProjectSettings/DocProjectSettings.vue';
import DocTypography from './documentation-components/Typography/DocTypography.vue';
import DocColors from './documentation-components/Colors/DocColors.vue';
import DocPageLayout from './documentation-components/PageLayout/DocPageLayout.vue';
import DocSvgIcon from './documentation-components/SvgIcon/DocSvgIcon.vue';
import DocFormItem from './documentation-components/FormItem/DocFormItem.vue';
import DocImplementationPlans from './documentation-components/ImplementationPlans/DocImplementationPlans.vue';
import DocComponentStatus from './documentation-components/ComponentStatus/DocComponentStatus.vue';
export default {
  components: {
    DocProjectSettings,
    DocTypography,
    DocColors,
    DocPageLayout,
    DocSvgIcon,
    DocFormItem,
    DocImplementationPlans,
    DocComponentStatus,
  },
  name: 'app',
		data(){
			return {
        currentSection: 'project-settings',
			};
    }
  }
</script>


