import Vue from 'vue';

const QuaverDefaultSettings = Vue.mixin({
  data() {
    return {
      get QuaverDefaultSettings() {
        return {
          ColorPrimary: "b400",
          ColorPrimaryHover: "b500",
          ColorSecondary: "m300",
          ColorSecondaryHover: "m400",
          ColorStateError: "r300",
          ColorStateSuccess: "g300",
          ColorStateInactive: "m500",
          PrimaryBg: "m100",
          TextColorPrimary: "m900",
          TextColorSecondary: "m600",
          FontSize: 16,
          FontSizeMobile: 14,
          FontFamilyPrimery: "'Circular Book', sans-serif",
          FontFamilySecondary: "'Circular Black', sans-serif",
          FontFamilyTertialy: "'Circular Medium', sans-serif",
          ElementSpacing: 15,
          ElementSpacingMobile: 10,
          BorderRadius: 2,
          BorderWidth: 1
        };
      },
    };
  },
});
export default QuaverDefaultSettings;


