<template>
  <div>
	  <h1>Typography</h1>
		<div class="text-group">
			<p>Default font size is {{ projectSettings.primaryFontSize }} px and the line height is {{ getBodyLineHeight }} px. These parameters define title styles according to its level.</p>
			<p>In order to get a specific text style, a corresponding tag or any other semantic tag with an appropriate class needs to be applied.</p>
		</div>
		<p>More information about typography styles can be found at <a href="https://ascapny.atlassian.net/wiki/spaces/PD/pages/524091393/Tokens" target="_blank">Confluence</a>.</p>
		<!-- table description start -->
		<table class="table-description">
				<tr>
						<th>Class</th>
						<th>Tag</th>
						<th>Desktop</th>
						<th>Mobile</th>
				</tr>
				<tr>
						<td><span class="class-name">default</span></td>
						<td>body, p, ul, ol, dl</td>
						<td>
							<ul class="style-list">
								<li>font-size: {{ projectSettings.primaryFontSize }}px;</li>
								<li>line-height: {{ getBodyLineHeight }}px;</li>
								<li>margin: 0 0 {{ getPSpacing }}px;</li>
								<li>font-family: {{ componentSettings.smallheading.fontFamily }};</li>
							</ul>
						</td>						
						<td>
							<ul class="style-list">
								<li>font-size: {{ projectSettings.primaryFontSizeMobile }}px;</li>
								<li>line-height: {{ getBodyLineHeightMobile }}px;</li>
								<li>margin: 0 0 {{ getPSpacingMobile }}px;</li>
							</ul>
						</td>
				</tr>
				<tr>
						<td><span class="class-name">t-title</span></td>
						<td>h1</td>
						<td>
							<ul class="style-list">
								<li>font-size: {{ getTitleFontSize }}px;</li>
								<li>line-height:  {{ getTitleLineHeight }}px;</li>
								<li>margin: 0 0 {{ getTitleSpacing }}px;</li>
								<li>font-family: {{ componentSettings.title.fontFamily }};</li>
							</ul>
						</td>						
