							<ul class="style-list">
								<li>font-size: {{ getTitleFontSizeMobile }}px;</li>
								<li>line-height:  {{ getTitleLineHeightMobile }}px;</li>
								<li>margin: 0 0 {{ getTitleSpacingMobile }}px;</li>
							</ul>
						</td>
				</tr>
				<tr>
						<td><span class="class-name">t-subtitle</span></td>
						<td>h2</td>
						<td>
							<ul class="style-list">
								<li>font-size: {{ getSubTitleFontSize }}px;</li>
								<li>line-height: {{ getSubTitleLineHeight }}px;</li>
								<li>margin: 0 0 {{ getSubTitleSpacing }}px;</li>
								<li>font-family: {{ componentSettings.subtitle.fontFamily }};</li>
							</ul>
						</td>						
						<td>
							<ul class="style-list">
								<li>font-size: {{ getSubTitleFontSizeMobile }}px;</li>
								<li>line-height:  {{ getSubTitleLineHeightMobile }}px;</li>
								<li>margin: 0 0 {{ getSubTitleSpacingMobile }}px;</li>
							</ul>
						</td>
				</tr>
				<tr>
						<td><span class="class-name">t-heading</span></td>
						<td>h3</td>
						<td>
							<ul class="style-list">
								<li>font-size: {{ getHeadingFontSize }}px;</li>
								<li>line-height: {{ getHeadingLineHeight }}px;</li>
								<li>margin: 0 0 {{ getHeadingSpacing }}px;</li>
								<li>font-family: {{ componentSettings.heading.fontFamily }};</li>
							</ul>
						</td>						
						<td>
							<ul class="style-list">
								<li>font-size: {{ getHeadingFontSizeMobile }}px;</li>
								<li>line-height:  {{ getHeadingLineHeightMobile }}px;</li>
								<li>margin: 0 0 {{ getHeadingSpacingMobile }}px;</li>
							</ul>
						</td>
				</tr>
				<tr>
						<td><span class="class-name">t-subheading</span></td>
						<td>h4</td>
						<td>
							<ul class="style-list">
								<li>font-size: {{ getSubHeadingFontSize }}px;</li>
								<li>line-height: {{ getSubHeadingLineHeight }}px;</li>
								<li>margin: 0 0 {{ getSubHeadingSpacing }}px;</li>
								<li>font-family: {{ componentSettings.subheading.fontFamily }};</li>
							</ul>
						</td>						
						<td>
							<ul class="style-list">
								<li>font-size: {{ getSubHeadingFontSizeMobile }}px;</li>
								<li>line-height:  {{ getSubHeadingLineHeightMobile }}px;</li>
								<li>margin: 0 0 {{ getSubHeadingSpacingMobile }}px;</li>
							</ul>
						</td>
				</tr>
				<tr>
						<td><span class="class-name">t-smallheading</span></td>
						<td>h5</td>
						<td>
							<ul class="style-list">
								<li>font-size: {{ getSmallHeadingFontSize }}px;</li>
								<li>line-height: {{ getSmallHeadingLineHeight }}px;</li>
								<li>margin: 0 0 {{ getSmallHeadingSpacing }}px;</li>
								<li>font-family: {{ componentSettings.smallheading.fontFamily }};</li>
							</ul>
						</td>						
						<td>
							<ul class="style-list">
								<li>font-size: {{ getSmallHeadingFontSizeMobile }}px;</li>
								<li>line-height:  {{ getSmallHeadingLineHeightMobile }}px;</li>
								<li>margin: 0 0 {{ getSmallHeadingSpacingMobile }}px;</li>
							</ul>
						</td>
				</tr>
		</table>
		<!-- table description end -->
		<!-- settings colums start -->
		<div class="settings-colums">
			<!-- settings column start -->
			<div class="settings-column">
				<!-- project settings start -->
				<dl class="project-settings">
					<dt><h3>Base Font Size:</h3></dt>
					<dd><span class="settings-title">$g-font-size</span>:  <input v-model="projectSettings.primaryFontSize" type="number" class="input-setting" />px</dd>
					<dd><span class="settings-title">$g-font-size-mobile</span>:  <input v-model="projectSettings.primaryFontSizeMobile" type="number" class="input-setting" />px</dd>
				</dl>
				<!-- project settings end -->
			</div>
			<!-- settings column end -->
		</div>
		<!-- settings colums end -->
		<!-- using example start -->
		<div class="using-example">
			<!-- view tab start -->
			<input name="tabs-01" type="radio" id="typography-preview-01" checked="checked" class="using-example__input"/>
			<label for="typography-preview-01" class="using-example__label first">Preview</label>
			<div class="using-example__panel">
				<div class="view">
					<!-- desktop start -->
					<div class="view__column desktop">
						<div class="view__content" v-bind:style="{ fontSize: projectSettings.primaryFontSize + 'px' }">
							<div class="t-title">H1 or Title</div>
							<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard</p>
							<div class="t-subtitle">H2 or Subtitle</div>
							<p>It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.</p>
							<div class="t-heading">H3 or Heading</div>
							<p>It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages.</p>
							<div class="t-subheading">H4 or Subheading</div>
							<p>It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.</p>
							<div class="t-smallheading">H5 or Smallheading</div>
							<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard</p>

						</div>
					</div>
					<!-- desktop end -->
					<!-- mobile start -->
					<div class="view__column mobile">
						<div class="view__content" v-bind:style="{ fontSize: projectSettings.primaryFontSizeMobile + 'px' }">
							<div class="t-title">H1 or Title</div>
							<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard</p>
							<div class="t-subtitle">H2 or Subtitle</div>
							<p>It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.</p>
							<div class="t-heading">H3 or Heading</div>
							<p>It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages.</p>
							<div class="t-subheading">H4 or Subheading</div>
							<p>It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged.</p>
							<div class="t-smallheading">H5 or Smallheading</div>
							<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard</p>

						</div>
					</div>
					<!-- mobile end -->
				</div>
			</div>
			<input name="tabs-01" type="radio" id="typography-preview-02" class="using-example__input"/>
			<label for="typography-preview-02" class="using-example__label last">Markup</label>
			<div class="using-example__panel">
				<div class="markup">
					<button class="copy">copy</button>
					<pre>
<span class="pln"></span><span class="tag">&lt;h1</span><span class="tag">&gt;</span>H1 or Title<span class="tag">&lt;/h1</span><span class="tag">&gt;</span>
<span class="com">&lt;!-- or any other tag with the class '.t-title' --&gt;</span><span class="pln"></span>
<span class="tag">&lt;h3 <span class="class">class</span><span class="pun">=</span><span class="name">"t-title"</span><span class="tag">&gt;</span></span>H1 or Title<span class="tag">&lt;/h3</span><span class="tag">&gt;</span>
<span class="pln"></span><span class="tag">&lt;p</span><span class="tag">&gt;</span>Lorem Ipsum is simply dummy text of the printing.<span class="tag">&lt;/p</span><span class="tag">&gt;</span>

<span class="pln"></span><span class="tag">&lt;h2</span><span class="tag">&gt;</span>H2 or Subitle<span class="tag">&lt;/h2</span><span class="tag">&gt;</span>
<span class="com">&lt;!-- or any other tag with the class '.t-subtitle' --&gt;</span><span class="pln"></span>
<span class="tag">&lt;div <span class="class">class</span><span class="pun">=</span><span class="name">"t-subtitle"</span><span class="tag">&gt;</span></span>H2 or Subitle<span class="tag">&lt;/div</span><span class="tag">&gt;</span>
<span class="pln"></span><span class="tag">&lt;p</span><span class="tag">&gt;</span>Lorem Ipsum is simply dummy text of the printing.<span class="tag">&lt;/p</span><span class="tag">&gt;</span>

<span class="pln"></span><span class="tag">&lt;h3</span><span class="tag">&gt;</span>H3 or Heading<span class="tag">&lt;/h3</span><span class="tag">&gt;</span>
<span class="com">&lt;!-- or any other tag with the class '.t-heading' --&gt;</span><span class="pln"></span>
<span class="tag">&lt;div <span class="class">class</span><span class="pun">=</span><span class="name">"t-heading"</span><span class="tag">&gt;</span></span>H3 or Heading<span class="tag">&lt;/div</span><span class="tag">&gt;</span>
<span class="pln"></span><span class="tag">&lt;p</span><span class="tag">&gt;</span>Lorem Ipsum is simply dummy text of the printing.<span class="tag">&lt;/p</span><span class="tag">&gt;</span>

<span class="pln"></span><span class="tag">&lt;h4</span><span class="tag">&gt;</span>H4 or Subheading<span class="tag">&lt;/h4</span><span class="tag">&gt;</span>
<span class="com">&lt;!-- or any other tag with the class '.t-subheading' --&gt;</span><span class="pln"></span>
<span class="tag">&lt;span <span class="class">class</span><span class="pun">=</span><span class="name">"t-subheading"</span><span class="tag">&gt;</span></span>H4 or Subheading<span class="tag">&lt;/span</span><span class="tag">&gt;</span>
<span class="pln"></span><span class="tag">&lt;p</span><span class="tag">&gt;</span>Lorem Ipsum is simply dummy text of the printing.<span class="tag">&lt;/p</span><span class="tag">&gt;</span>

<span class="pln"></span><span class="tag">&lt;h5</span><span class="tag">&gt;</span>H5 or Smallheading<span class="tag">&lt;/h5</span><span class="tag">&gt;</span>
<span class="com">&lt;!-- or any other tag with the class '.t-smallheading' --&gt;</span><span class="pln"></span>
<span class="tag">&lt;strong <span class="class">class</span><span class="pun">=</span><span class="name">"t-smallheading"</span><span class="tag">&gt;</span></span>H5 or Smallheading<span class="tag">&lt;/strong</span><span class="tag">&gt;</span>
<span class="pln"></span><span class="tag">&lt;p</span><span class="tag">&gt;</span>Lorem Ipsum is simply dummy text of the printing.<span class="tag">&lt;/p</span><span class="tag">&gt;</span>
					</pre>					
				</div>
			</div>
		</div>
		<!-- using example end -->
  </div>
</template>
<script>
	import quaverDefaultSettings from '../../mixins/quaverDefaultSettings';
	export default {
		props: {
		},
		data(){
			return {
				projectSettings: {
					primaryFontSize: null,
					primaryFontSizeMobile: null,
				},
				componentSettings: {
					body: {
						lineHeight: 22,
						fontFamily: null,
					},
					title: {
						fontSize: 28,
						lineHeight: 30,
						spacing: 20,
						fontFamily: null,
						fontSizeMobile: 24,
						spacingMobile: 16
					},
					subtitle: {
						fontSize: 24,
						lineHeight: 31,
						spacing: 8,
						fontFamily: null,
						fontSizeMobile: 22,
						spacingMobile: 8,
					},
					heading: {
						fontSize: 20,
						lineHeight: 26,
						spacing: 4,
						fontFamily: null,
						fontSizeMobile: 18,
						spacingMobile: 4,
					},
					subheading: {
						fontSize: 18,
						lineHeight: 22,
						spacing: 4,
						fontFamily: null,
						fontSizeMobile: 18,
						spacingMobile: 4,
					},
					smallheading: {
						fontSize: 16,
						lineHeight: 22,
						spacing: 4,
						fontFamily: null,
						fontSizeMobile: 14,
						spacingMobile: 4,
					}
				},
			};
		},
		created: function () {
			this.projectSettings.primaryFontSize = this.QuaverDefaultSettings.FontSize;
			this.projectSettings.primaryFontSizeMobile = this.QuaverDefaultSettings.FontSizeMobile;
			this.projectSettings.primaryColorText = this.QuaverDefaultSettings.ColorText;
			this.componentSettings.body.fontFamily = this.QuaverDefaultSettings.FontFamilyPrimary;
			this.componentSettings.title.fontFamily = this.QuaverDefaultSettings.FontFamilySecondary;
			this.componentSettings.subtitle.fontFamily = this.QuaverDefaultSettings.FontFamilySecondary;
			this.componentSettings.heading.fontFamily = this.QuaverDefaultSettings.FontFamilySecondary;
			this.componentSettings.subheading.fontFamily = this.QuaverDefaultSettings.FontFamilySecondary;
			this.componentSettings.smallheading.fontFamily = this.QuaverDefaultSettings.FontFamilySecondary;
  	},
		computed: {
			/* Title */
    	getTitleFontSize:function() {
        return Math.round(this.componentSettings.title.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getTitleFontSizeMobile:function() {
        return Math.round(this.componentSettings.title.fontSizeMobile/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getTitleLineHeight:function() {
        return Math.round(this.componentSettings.title.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getTitleLineHeightMobile:function() {
        return Math.round(this.componentSettings.title.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getTitleSpacing:function() {
        return Math.round(this.componentSettings.title.spacing/this.componentSettings.title.fontSize * this.componentSettings.title.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getTitleSpacingMobile:function() {
        return Math.round(this.componentSettings.title.spacingMobile/this.componentSettings.title.fontSizeMobile * this.componentSettings.title.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			/* SubTitle */
			getSubTitleFontSize:function() {
        return Math.round(this.componentSettings.subtitle.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSubTitleFontSizeMobile:function() {
        return Math.round(this.componentSettings.subtitle.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			getSubTitleLineHeight:function() {
        return Math.round(this.componentSettings.subtitle.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSubTitleLineHeightMobile:function() {
        return Math.round(this.componentSettings.subtitle.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getSubTitleSpacing:function() {
        return Math.round(this.componentSettings.subtitle.spacing/this.componentSettings.subtitle.fontSize * this.componentSettings.subtitle.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSubTitleSpacingMobile:function() {
        return Math.round(this.componentSettings.subtitle.spacingMobile/this.componentSettings.subtitle.fontSizeMobile * this.componentSettings.subtitle.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			/* Heading */
			getHeadingFontSize:function() {
        return Math.round(this.componentSettings.heading.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getHeadingFontSizeMobile:function() {
        return Math.round(this.componentSettings.heading.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			getHeadingLineHeight:function() {
        return Math.round(this.componentSettings.heading.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getHeadingLineHeightMobile:function() {
        return Math.round(this.componentSettings.heading.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getHeadingSpacing:function() {
        return Math.round(this.componentSettings.heading.spacing/this.componentSettings.heading.fontSize * this.componentSettings.heading.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getHeadingSpacingMobile:function() {
        return Math.round(this.componentSettings.heading.spacingMobile/this.componentSettings.heading.fontSizeMobile * this.componentSettings.heading.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			/* SubHeading */
			getSubHeadingFontSize:function() {
        return Math.round(this.componentSettings.subheading.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSubHeadingFontSizeMobile:function() {
        return Math.round(this.componentSettings.subheading.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			getSubHeadingLineHeight:function() {
        return Math.round(this.componentSettings.subheading.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSubHeadingLineHeightMobile:function() {
        return Math.round(this.componentSettings.subheading.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getSubHeadingSpacing:function() {
        return Math.round(this.componentSettings.subheading.spacing/this.componentSettings.subheading.fontSize * this.componentSettings.subheading.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSubHeadingSpacingMobile:function() {
        return Math.round(this.componentSettings.subheading.spacingMobile/this.componentSettings.subheading.fontSizeMobile * this.componentSettings.subheading.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			/* SmallHeading */
			getSmallHeadingFontSize:function() {
        return Math.round(this.componentSettings.smallheading.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSmallHeadingFontSizeMobile:function() {
        return Math.round(this.componentSettings.smallheading.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			getSmallHeadingLineHeight:function() {
        return Math.round(this.componentSettings.smallheading.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSmallHeadingLineHeightMobile:function() {
        return Math.round(this.componentSettings.smallheading.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getSmallHeadingSpacing:function() {
        return Math.round(this.componentSettings.smallheading.spacing/this.componentSettings.smallheading.fontSize * this.componentSettings.smallheading.fontSize/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getSmallHeadingSpacingMobile:function() {
        return Math.round(this.componentSettings.smallheading.spacingMobile/this.componentSettings.smallheading.fontSizeMobile * this.componentSettings.smallheading.fontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile * this.projectSettings.primaryFontSizeMobile);
			},
			/* body */
			getBodyLineHeight:function() {
        return Math.round(this.componentSettings.body.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSize);
			},
			getBodyLineHeightMobile:function() {
        return Math.round(this.componentSettings.body.lineHeight/this.QuaverDefaultSettings.FontSize * this.projectSettings.primaryFontSizeMobile);
			},
			getPSpacing:function() {
        return Math.round(this.QuaverDefaultSettings.ElementSpacing * this.projectSettings.primaryFontSize/this.QuaverDefaultSettings.FontSize);
			},
			getPSpacingMobile:function() {
        return Math.round(this.QuaverDefaultSettings.ElementSpacingMobile * this.projectSettings.primaryFontSizeMobile/this.QuaverDefaultSettings.FontSizeMobile);
      }
    }
	}
</script>


