<template>
  <!-- colors section start -->
  <div>
    <!-- component content start -->
    <div class="component-content">
      <h1>Page Layout</h1>
      <div class="text-group">
        <p>Base Page layout consists of a <span class="class-name" @mouseleave="hoverKeyWord(structure.Page)" @mouseover="hoverKeyWord(structure.Page)">Main Content </span> area and a sidebar with a <span class="class-name" @mouseleave="hoverKeyWord(structure.Menu)" @mouseover="hoverKeyWord(structure.Menu)">Main Navigation</span>.</p>
      </div>
      <div class="text-group">
        Also, can be used <span class="class-name" @mouseleave="hoverKeyWord(structure.Header)" @mouseover="hoverKeyWord(structure.Header)">Page Header</span>, <span class="class-name" @mouseleave="hoverKeyWord(structure.Footer)" @mouseover="hoverKeyWord(structure.Footer)">Page Footer</span>, <span class="class-name" @mouseleave="hoverKeyWord(structure.Content)" @mouseover="hoverKeyWord(structure.Content)">Content</span> , <span class="class-name" @mouseleave="hoverKeyWord(structure.Module)" @mouseover="hoverKeyWord(structure.Module)">Modules</span>, and <span class="class-name" @mouseleave="hoverKeyWord(structure.Message)" @mouseover="hoverKeyWord(structure.Message)">Global Message</span>.
      </div> 
      <!-- using example start -->
      <div class="using-example">
          <!-- view tab start -->
          <input name="tabs-01" type="radio" id="pagelayout-preview-01" checked="checked" class="using-example__input"/>
          <label for="pagelayout-preview-01" class="using-example__label first">Preview</label>
          <div class="using-example__panel">
            <div class="view">
              <!-- desktop start -->
              <div class="view__column desktop">
                <div class="view__content">
                   <!-- layout page start -->
                    <div class="l-body" :class="this.structure.Body">
                      <!-- message start -->
                      <div class="c-message" :class="this.structure.Message">
                        Message Content
                        <svg class="ico-close" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><line x1="18" y1="6" x2="6" y2="18"/><line x1="6" y1="6" x2="18" y2="18"/></svg>
                      </div>
                      <!-- message end -->
                      <!-- main-content start -->
                      <div class="l-main-content">
                        <!-- menu start -->
                        <aside class="menu" :class="this.structure.Menu">
                          <h3>Menu</h3>
                          <svg class="menu-ico" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><line x1="3" y1="12" x2="21" y2="12"/><line x1="3" y1="6" x2="21" y2="6"/><line x1="3" y1="18" x2="21" y2="18"/></svg>
                        </aside>
                        <!-- menu end -->
                        <!-- layout content start -->
                        <main class="l-page" :class="this.structure.Page">
                          <!-- header start -->
                          <header class="c-header" :class="this.structure.Header">
                            <h1>Header</h1>
                          </header>
                          <!-- header end -->
                          <!-- module start -->
                          <div class="c-module" :class="this.structure.Module">
                            <h5>Module</h5>
                            <p>Lorem Ipsum is simply dummy text of the printin</p>
                          </div>
                          <!-- module end -->
                          <!-- layout wrap start -->
                          <div class="l-content" :class="this.structure.Content">
                            <div class="l-content_inner">
                              <h3>Content</h3>
                              <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                              <p>Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.</p>
                              <!-- comments start -->
                              <div class="comments">
                                <span class="comment-left"><span class="spacing-default">36px</span><span class="spacing-article">36px + 30px + 100%/12</span></span>
                                <span class="comment-right"><span class="spacing-default">36px</span></span>
                              </div>
                              <!-- comments end -->
                            </div>
                          </div>
                          <!-- layout wrap end -->
                          <!-- footer start -->
                          <footer class="c-footer" :class="this.structure.Footer">
                            Footer
                          </footer>
                          <!-- footer end -->
                        </main>
                        <!-- layout content end -->
                      </div>
                    </div>
                    <!-- layout main-content end -->
                </div>
              </div>
              <!-- desktop end -->
              <!-- mobile start -->
              <div class="view__column mobile">
                  <div class="view__content">
                    <!-- layout page start -->
                    <div class="l-body" :class="this.structure.Body">
                      <!-- message start -->
                      <div class="c-message" :class="this.structure.Message">
                        Message Content
                        <svg class="ico-close" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><line x1="18" y1="6" x2="6" y2="18"/><line x1="6" y1="6" x2="18" y2="18"/></svg>
                      </div>
                      <!-- message end -->
                      <!-- main-content start -->
                      <div class="l-main-content">
                        <!-- menu start -->
                        <aside class="menu" :class="this.structure.Menu">
                          <h3>Menu</h3>
                          <svg class="menu-ico" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><line x1="3" y1="12" x2="21" y2="12"/><line x1="3" y1="6" x2="21" y2="6"/><line x1="3" y1="18" x2="21" y2="18"/></svg>
                        </aside>
                        <!-- menu end -->
                        <!-- layout content start -->
                        <main class="l-page" :class="this.structure.Page">
                          <!-- header start -->
                          <header class="c-header" :class="this.structure.Header">
                            <h1>Header</h1>
                          </header>
                          <!-- header end -->
                          <!-- module start -->
                          <div class="c-module" :class="this.structure.Module">
                            <h5>Module</h5>
                            <p>Lorem Ipsum is simply dummy text of the printin</p>
                          </div>
                          <!-- module end -->
                          <!-- layout wrap start -->
                          <div class="l-content" :class="this.structure.Content">
                            <div class="l-content_inner">
                              <h3>Content</h3>
                              <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                              <p>Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book.</p>
                              <!-- comments start -->
                              <div class="comments">
                                <span class="comment-left"><span class="spacing-default">16px</span></span>
                                <span class="comment-right"><span class="spacing-default">16px</span></span>
                              </div>
                            </div>
                          </div>
                          <!-- layout wrap end -->
                          <!-- footer start -->
                          <footer class="c-footer" :class="this.structure.Footer">
                            Footer
                          </footer>
                          <!-- footer end -->
                        </main>
                        <!-- layout content end -->
                      </div>
                    </div>
                    <!-- layout main-content end -->
                  </div>
                </div>
              <!-- mobile end -->
            </div>
          </div>
          <!-- view tab end -->
          <!-- markup tab start -->
          <input name="tabs-01" type="radio" id="pagelayou-markup-01" class="using-example__input"/>
          <label for="pagelayou-markup-01" class="using-example__label last">Markup</label>
          <div class="using-example__panel">
           
          </div>
          <!-- markup tab end -->
      </div>
      <!-- using example end -->
      <h2>Main Content</h2>
      <!-- table description start -->
      <table class="table-description">
        <tr>
          <th>Mode</th>
          <th>Description</th>
        </tr>
        <tr>
          <td><span class="attr-name">default</span></td>
          <td>Left and right spacing of the content are 36px and 16px for mobile devices</td>
        </tr>
        <tr>
          <td><span class="attr-name">Centered Content</span></td>
          <td>Content is centered vertically</td>
        </tr>
        <tr>
          <td><span class="attr-name">Article Page</span></td>
          <td>Left and right spacing of the content are 36px + 30px + 100%/12 and 16px for mobile devices</td>
        </tr>
      </table>
      <!-- table description end -->
      <h2>Main Navigation</h2>
      <!-- table description start -->
      <table class="table-description">
        <tr>
          <th>Mode</th>
          <th>Description</th>
        </tr>
        <tr>
          <td><span class="attr-name">default</span></td>
          <td>Main Navigatio is positioned on the left of the page</td>
        </tr>
        <tr>
          <td><span class="attr-name">Menu on top</span></td>
          <td>Main Navigatio is positioned on the top of the page</td>
        </tr>
      </table>
      <!-- table description end -->
    </div>
    <!-- table description end -->
    <!-- properties content start -->
    <aside class="properties-content">
      <!-- parameters list start -->
      <dl class="parameters">
        <dt>Mode:</dt>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Content['l-content--centered']"
                  id="label-1"
          >
          <label class="attr-label" for="label-1">Centered Content</label>
        </dd>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Content['l-content--article']"
                  id="label-2"
          >
          <label class="attr-label" for="label-2">Article Page</label>
        </dd>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Body['l-body--menu-top']"
                  id="label-3"
          >
          <label class="attr-label" for="label-3">Menu on top</label>
        </dd>
        <dt>Structure:</dt>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Header['is-visible']"
                  id="label-4"
          >
          <label class="attr-label" for="label-4">Page Header</label>
        </dd>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Footer['is-visible']"
                  id="label-5"
          >
          <label class="attr-label" for="label-5">Page Footer</label>
        </dd>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Message['is-visible']"
                  id="label-6"
          >
          <label class="attr-label" for="label-6">Message</label>
        </dd>
        <dd>
          <input  type="checkbox"
                  v-model="structure.Module['is-visible']"
                  id="label-7"
          >
          <label class="attr-label" for="label-7">Module</label>
        </dd>
      </dl>
      <!-- parameters list end -->
    </aside>
  </div>
  <!-- colors section end -->
</template>
<script>
  import Vue from 'vue';
  import Clipboard from 'v-clipboard';
  Vue.use(Clipboard);
	export default {
    
		props: {
		},
		data(){
			return {
        activeMode: 'default',
        structure: {
          Body: {
            'l-body--menu-top': false
          },
          Page: {
            'has-focuse': false
          },
          Header: {
            'is-visible': false,
            'has-focuse': false
          },
          Menu: {
            'has-focuse': false
          },
          Footer: {
            'is-visible': false,
            'has-focuse': false
          },
          Module: {
            'is-visible': false,
            'has-focuse': false
          },
          Message: {
            'is-visible': false,
            'has-focuse': false
          },
          Content: {
            'has-focuse': false,
            'is-article': false,
            'l-content--centered': false,
            'l-content--article': false
          },
        }
			};
    },
		methods: {
      hoverKeyWord:function(name) {
        name['has-focuse'] = !name['has-focuse'];
        name['is-visible'] = true;
      },
    }
	}
</script>
<style>
  /* body */
  .l-body {
    height: 400px;
    font-size: 11px;
    border: 1px solid #ccc;
    background: #F6F8FA;
    display: flex;
    flex-direction: column;
  }
  /* message */
  .c-message {
    display: none;
    padding: 3px 10px;
    text-align: center;
    background: #F4CA08;
    border-bottom: 1px solid #ccc;
    position: relative;
  }
  .c-message.is-visible { display: block; }
  /* main content */
  .l-main-content {
    display: flex;
    flex-grow: 1;
  }
  /* menu */
  .menu {
    width: 80px;
    flex: 0 0 80px;
    background: #004183;
    color: #fff;
    text-align: center;
    padding: 10px 5px;
  }
  .menu-ico { display: none; }
  .menu h3 { 
    color: #fff !important;
  }
  .l-body--menu-top .menu {
      position: absolute;
      width: auto;
      padding: 0 7px;
  }
  /* page */
  .l-page {
    flex: 1 1 auto;
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  .l-page p { margin: 0 0 5px; }
  /* content */
  .l-content {
    flex: 1;
    padding: 15px 18px;
    position: relative;
  }
  /* content inner */
  .l-content_inner { }
  .l-content--centered {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .l-content--full-width {
    padding-left: 0;
    padding-right: 0;
  }
  .l-content--article {
    padding-left: calc(16px*2 + 100%/12);
    padding-right: calc(16px*2 + 100%/12);
  }
  /* comments */
  .comments {
    position: absolute;
    left: 0;
    right: 0;
    margin: -20px 0 0;
    border: solid #ce3535;
    border-width: 0 1px;
  }
  .comments span.comment-left, .comments span.comment-right {
    position: relative;
    float: left;
    margin: 0 17px;
    color: #ce3535;
    border: solid #ce3535;
    border-width: 0 0 0 1px;
    padding: 20px 0 0 5px;
    white-space: nowrap;
    min-height: 15px;
  }
  .comments span.comment-right {
    float: right;
    padding-left: 0;
    padding-right: 10px;
    border-left: 0;
    border-right-width: 1px;
  }
  .l-content--article .comments span.comment-left {
    margin-left: calc(16px*2 + 100%/12 - 2px);
  }
  .l-content--article .comments span.comment-right {
    margin-right: calc(16px*2 + 100%/12 - 2px);
  }
  .desktop .l-content--article .spacing-default { display: none; }
  .desktop .spacing-article { display: none; }
  .desktop .l-content--article .spacing-article { display: block; }
  .mobile .comments span.comment-left {
    margin-left: 8px;
  }
  .mobile .comments span.comment-right {
    margin-right: 8px;
  }
  /* header */
  .c-header {
    display: none;
    background: #fafafa;
    border-bottom: 1px solid #ececec;
    padding: 7px 18px;
  }
  .c-header.is-visible { display: block; }
  .has-focuse { position: relative; }
  .has-focuse:after {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    right: -1px;
    bottom: -1px;
    /* box-shadow: inset 0px 0px 0px 2px #55b149; */
    border: 2px solid #80d462;
  }
  .l-body--menu-top .c-header {
    background: #004183;
    color: #fff;
    text-align: center;
  }
  .c-header h1 { 
    margin: 0;
    font-size: 14px;
  }
  .l-body--menu-top .c-header h1 { color: #fff; }
  .c-footer {
    display: none;
    border-top: 1px solid #ccc;
    /* margin-top: auto; */
    padding: 5px 18px;
    text-align: center;
    background: #fff;
  }
  .c-footer.is-visible { display: block; }
  .info { background: #eee; }
  .c-module {
    display: none;
    background: #175da7;
    color: #fff;
    text-align: center;
    padding: 15px 18px;
  }
  .c-module.is-visible { display: block; }
  
  .ico-close {
    position: absolute;
    right: 5px;
    top: 50%;
    margin: -5px 0 0 0;
    width: 10px;
    height: 10px;
  }
  /* mobile version */
  .mobile .l-body {
    width: 100%;
    height: 400px;
  }
  .mobile .c-header {
    padding-left: 9px;
    padding-right: 9px;
  }
  .mobile .c-header h1 { font-size: 13px; }
  .mobile .menu {
    width: 25px;
    flex-basis: 25px;
    padding: 0px 5px;
  }
  .mobile .menu-ico, .l-body--menu-top .menu-ico {
    display: block;
    width: 15px;
    margin: 0 auto;
  }
  .mobile .menu h3, .l-body--menu-top .menu h3 { display: none; }
  .mobile .c-module {
    padding-left: 9px;
    padding-right: 9px;
  }
  .mobile .l-content { padding: 10px 9px; }
  .mobile .l-content--article {
  }
  
</style>


